* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	font-family: 'Nunito', sans-serif;
}

button {
	border: none;
	outline: none;
	width: 100%;
	padding: 15px 0;
	color: #fff;
	font-size: 16px;
	letter-spacing: 1px;
	background: #603bbb;
	cursor: pointer;
}

.login {
	width: 100%;
	min-height: 100vh;
	padding: 0 20px;
	background: #e9e9e9;
	background-position: center; /* Center the image */
	background-size: cover;
	background-image: url('https://cdn.hipwallpaper.com/i/37/30/0EF75T.jpg');
	display: flex;
}

.login .loginContainer {
	padding: 60px;
	margin: auto;
	width: 100%;
	max-width: 520px;
	min-height: 600px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	background: radial-gradient(
		ellipse at left bottom,
		rgba(22, 24, 47, 1) 0%,
		rgba(38, 20, 72, 0.9) 59%,
		rgba(17, 27, 75, 0.9) 100%
	);
	box-shadow: 0 50px 70px -20px rgba(0, 0, 0, 0.8);
}

.login .loginContainer label {
	color: white;
	margin: 14px 0;
	display: block;
	font-size: 22px;
	line-height: 1;
}

.login .loginContainer input {
	width: 100%;
	border: none;
	outline: none;
	font-size: 19px;
	padding: 10px;
	background: rgba(255, 255, 255, 0.1);
	color: #fff;
	letter-spacing: 1px;
}

.login .loginContainer .btnContainer {
	width: 100%;
	padding: 24px 0;
}

.login .loginContainer .btnContainer p {
	margin: 14px 0 0 0;
	text-align: right;
	color: #fff;
}

.login .loginContainer .btnContainer p span {
	color: yellow;
	font-weight: 500;
	letter-spacing: 0.5px;
	margin-left: 5px;
	cursor: pointer;
	transition: all 400ms ease-in-out;
}

.login .loginContainer .btnContainer p span:hover {
	color: red;
}

.login .loginContainer .errorMsg {
	color: red;
	font-size: 16px;
}

