.hero nav {
	background: #f19345;
	width: 100%;
	padding: 20px 40px;
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.hero nav h2 {
	color: #fff;
}

.hero nav button {
	width: 86px;
	padding: 8px 0;
	background: #775fa8;
	border-radius: 2rem;
}
